/*清除浮动*/
.clear:after {
    content: '.';
    display: block;
    visibility: hidden;
    height: 0;
    clear: both;
}
.clear {
    zoom: 1;
}
.float-right {
    float: right;
}
/*文本居中*/
.text-center {
    text-align: center;
}
/*div居中*/
.box-center {
    margin: 0 auto;
}
/*绝对居中*/
.absolute-center {
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
/*绝对水平居中*/
.absolute-horizontal-center {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
/*绝对垂直居中*/
.absolute-vertical-center {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
/*使用方法：再p标签加上改类名并配置line height和height属性，实例已给出*/
/*多行文本溢出补齐....文本*/
.overflow-omit {
    position: relative;
    /*line-height: 1rem;*/
    /*height: 2rem;*/
    overflow: hidden;
}
.overflow-omit::after {
    content: "...";
    font-weight: bold;
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0 20px 1px 45px;
    background: url(http://newimg88.b0.upaiyun.com/newimg88/2014/09/ellipsis_bg.png) repeat-y;
}
